
 .centerBox {
  position: relative;  
  top: 75px;
  height: 515px;                 
  width: 550px;
  margin: 0 auto 0;
  border: solid;
  border-width: thin;
  border-radius: 15px;
  background-color: #D8D8D8;
  border: solid;
  border-width: 2px;
  border-color: #AE7FC3;
}


.head {
  background-color: #303030;
}

.head2 {
  background-color: #D8D8D8;
  color: #303030;
}

.info {
  border: solid;
  border-width: thin;
  border-radius: 15px;
  width: 94%;
  display: inline-block;
}

 .men {
  font-family: Menlo;
  font-size:  14px;
}

.box {
  position: relative;
  background-color: #181818;
  overflow: scroll;
  height: 200px; 
  color: #D8D8D8;
  margin-left:0;
}

.nav-link, 
.nav-link:hover {
  background-color: #D8D8D8;
  color: #AE7FC3;
  border: solid;
  border-width: thin;
  border-radius: 5px;
  border-color: #AE7FC3; 
}
.nav-tabs .nav-item:visited{
  background-color: #D8D8D8;
  color: #AE7FC3;
  border: solid;
  border-width: 2px;
  border-color: #AE7FC3;

}

.linked {
  color: white;
  text-decoration: none;
}
.linked:hover {
  color: #AE7FC3;
  text-decoration: none;
}
.purpBorder {
  border: solid;
  border-width: 2px;
  border-radius: 5px;
  border-color: #AE7FC3;
}
.table td{
  border-top: #AE7FC3 solid 1px;
}
.showbox {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
.showbox.scale:hover{
  cursor:pointer;
  -webkit-transform:scale(1.5);
  -moz-transform:scale(1.5);
  -o-transform:scale(1.5);
  -ms-transform:scale(1.5);
  transform:scale(1.5)
}
.medi {
  transition: all .3s ease-in-out;
  border: solid;
  border-color: #AE7FC3;
  border-radius: 5px;;
}
.medi:hover {
  border: none;
  border-radius: 50%;
  box-shadow: 0px 8px 6px -4px #AE7FC3;
}